.tab {
    display: flex;
    align-items: center;
    border-right: 3px solid var(--dark-main-color);
    padding: 10px 20px;    
    border-radius: 0px 25px 0px 0px;
}

.tab:hover {    
    cursor: pointer;
}

.active {
    color: var(--secondary-font-color);
    box-shadow: -30px 0px 25px -10px rgb(0 101 97 / 45%) inset;
}