.panel {
    background-color: var(--main-color);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    -webkit-box-shadow: 7px 7px 10px 0px rgba(112, 120, 126, 0.32);
    -moz-box-shadow: 7px 7px 10px 0px rgba(112, 120, 126, 0.32);
    box-shadow: 7px 7px 10px 0px rgba(112, 120, 126, 0.32);
    margin-bottom: 20px;
}