.creditForm {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top: 20px;
}

.creditFormInput {
    margin-left: 10px;
    color: var(--main-font-color);
}

.creditFormInput:focus {
    outline: 5px solid var(--secondary-color-b) !important;
}

.active {
    display: flex;
}